<template>
  <div class="tab-model">
    <div class="tab-button-div">
      <BasicForm @register="formRegister" @advanced-change="advancedChange" />
    </div>

    <div id="integrated-vehicle-tab-content" class="tab-table-div tab-table-content">
      <a-tabs v-model:activeKey="activeKey" @change="handleTabsChange">
        <a-tab-pane key="1" tab="统计报表">
          <BasicTable @register="registerTable" @resize-column="(w, col) => (col.width = w)">
            <template #toolbar>
              <a-button class="yellow-btn" :preIcon="IconEnum.EXPORT" @click="handleExport">{{
                t('common.action.export')
              }}</a-button>
            </template>
          </BasicTable>
        </a-tab-pane>
        <a-tab-pane key="2" tab="汇总" force-render>
          <BasicTable @register="registerTotalTable" @resize-column="(w, col) => (col.width = w)">
            <template #toolbar>
              <a-button class="yellow-btn" :preIcon="IconEnum.EXPORT" @click="handleTotalExport">{{
                t('common.action.export')
              }}</a-button>
            </template>
          </BasicTable>
        </a-tab-pane>
      </a-tabs>
    </div>
  </div>
</template>
<script lang="ts" setup>
  import { onMounted, ref, reactive } from 'vue';
  import { useMessage } from '/@/hooks/web/useMessage';
  import { useI18n } from '/@/hooks/web/useI18n';
  import { IconEnum } from '/@/enums/appEnum';
  import { BasicForm, useForm } from '@/components/Form';
  import { BasicTable, useTable } from '/@/components/Table';
  import { exportExcelFile } from '/@/utils/file/download';
  import { urlToPath } from '/@/utils/route';
  import {
    columns,
    totalColumns,
    getFormSearchSchemas,
    getPage,
    exportExcel,
    getTotalList,
    exportTotalExcel,
  } from './data';

  const { t } = useI18n();
  const { createConfirm, createMessage } = useMessage();
  const stationParams = reactive({ organId: null });
  const { params: routeQuery } = urlToPath();
  const activeKey = ref('1');

  const [formRegister, { resetFields, setFieldsValue, getFieldsValue }] = useForm({
    labelWidth: 120,
    baseColProps: { span: 8 },
    actionColOptions: {
      span: 24,
    },
    autoAdvancedLine: 1,
    schemas: getFormSearchSchemas(stationParams),
    compact: true,
    showAdvancedButton: true,
    resetButtonOptions: {
      onClick: async () => {
        await resetFields();
        stationParams.organId = null;
        reload();
        reloadTotal();
      },
    },
    submitFunc: () => {
      reload();
      reloadTotal();
      return Promise.resolve();
    },
  });

  const getParam = () => {
    const query = { ...getFieldsValue() };
    query.stationId = null;
    if (query.stationIds && query.stationIds.length > 0) {
      query.stationId = query.stationIds.join(',');
    }
    let param = {} as any;
    for (const key in query) {
      if (key === 'stationIds') {
        continue;
      }
      if (key === 'nextCheck' || key === 'insuranceExpire') {
        if (query[key] && query[key] instanceof Array) {
          param[key + 'StartDate'] = query[key][0];
          param[key + 'EndDate'] = query[key][1];
        }
      } else {
        param[key] = query[key];
      }
    }
    return param as any;
  };

  const getPageByParam = async () => {
    const paginationRef = getPaginationRef() as any;
    const query = getParam();
    query.pageNo = paginationRef.current;
    query.pageSize = paginationRef.pageSize;
    return await getPage(query);
  };
  const [registerTable, { reload, redoHeight, getPaginationRef }] = useTable({
    api: getPageByParam,
    target: '#integrated-vehicle-tab-content',
    targetTabKey: '1',
    targetTabValue: activeKey,
    columns: columns,
    useSearchForm: false,
    bordered: true,
    showTableSetting: false,
    tableSetting: { fullScreen: true },
    showIndexColumn: true,
  });

  const getTotalByParam = async () => {
    const query = getParam();
    return await getTotalList(query);
  };
  const [registerTotalTable, { reload: reloadTotal, redoHeight: redoHeightTotal }] = useTable({
    api: getTotalByParam,
    target: '#integrated-vehicle-tab-content',
    targetTabKey: '2',
    targetTabValue: activeKey,
    columns: totalColumns,
    useSearchForm: false,
    bordered: true,
    pagination: false,
    showTableSetting: false,
    tableSetting: { fullScreen: true },
    showIndexColumn: true,
  });

  function handleExport() {
    createConfirm({
      title: t('common.message.confirmTitle'),
      iconType: 'warning',
      content: t('common.message.exportMessage'),
      async onOk() {
        const query = getParam();
        query.filename = '统计报表.xls';
        await exportExcel(query).then((res) => {
          exportExcelFile(res?.data, query.filename);
        });
        query.filename = null;
        createMessage.success(t('common.exportSuccessText'));
      },
    });
  }

  function handleTotalExport() {
    createConfirm({
      title: t('common.message.confirmTitle'),
      iconType: 'warning',
      content: t('common.message.exportMessage'),
      async onOk() {
        const query = getParam();
        query.filename = '汇总.xls';
        await exportTotalExcel(query).then((res) => {
          exportExcelFile(res?.data, query.filename);
        });
        query.filename = null;
        createMessage.success(t('common.exportSuccessText'));
      },
    });
  }

  const handleTabsChange = (activeKey) => {
    if (activeKey === '1') {
      reload();
    } else if (activeKey === '2') {
      reloadTotal();
    }
  };

  const advancedChange = () => {
    if (activeKey.value === '1') {
      redoHeight();
    } else if (activeKey.value === '2') {
      redoHeightTotal();
    }
  };

  onMounted(() => {
    const params = { ...routeQuery };
    if (params.stationIds) {
      params.stationIds = params.stationIds.split(',');
    } else {
      params.stationIds = [];
    }
    //驾驶舱维度判断
    if (params.selectType == 'station') {
      params.stationIds = params.id;
    }
    if (params.selectType == 'company') {
      params.stationOrganId = params.id;
    }
    setFieldsValue(params);
  });
</script>
<style lang="less" scoped>
  @import '/@/design/biz/bizList.less';
</style>
