import { defHttp } from '/@/utils/http/axios';
import { BasicColumn } from '/@/components/Table';
import { useRender } from '@/components/Table';
import { FormSchema } from '@/components/Form';
import * as CommonApi from '/@/api/baseset/common';
import { useUserStore } from '/@/store/modules/user';

export enum Api {
  PAGE_URL = '/bi/integrated/vehicle/page',
  EXPORT_EXCEL_URL = '/bi/integrated/vehicle/export-excel',
  LIST_TOTAL_URL = '/bi/integrated/vehicle/getTotalList',
  EXPORT_TOTAL_EXCEL_URL = '/bi/integrated/vehicle/total/export-excel',
  LIST_ORG_URL = '/bi/common/org/info',
}

// 分页获取车辆信息列表
export const getPage = (params) =>
  defHttp.get(
    {
      url: Api.PAGE_URL,
      params,
    },
    { isOnlyResult: true },
  );

// 导出车辆信息列表
export const exportExcel = (params) =>
  defHttp.download(
    {
      url: Api.EXPORT_EXCEL_URL,
      params,
    },
    { isReturnNativeResponse: true },
  );

// 获取车辆信息汇总列表
export const getTotalList = (params) =>
  defHttp.get(
    {
      url: Api.LIST_TOTAL_URL,
      params,
    },
    { isOnlyResult: true },
  );

// 导出车辆信息汇总列表
export const exportTotalExcel = (params) =>
  defHttp.download(
    {
      url: Api.EXPORT_TOTAL_EXCEL_URL,
      params,
    },
    { isReturnNativeResponse: true },
  );

// 获得组织列表
export const getOrgList = (params) =>
  defHttp.get(
    {
      url: Api.LIST_ORG_URL,
      params,
    },
    { isOnlyResult: true },
  );

const statusColor = {
  1: '#0055ff', // 未来30天内
  2: '#e0001b', // 小于当前日期的
};
export const columns: BasicColumn[] = [
  {
    title: '所属公司',
    dataIndex: 'stationOrganName',
    headAlign: 'center',
    align: 'left',
    width: 220,
    resizable: true,
    ifShow: useUserStore().isMultiOrganization,
  },
  {
    title: '所属电站',
    dataIndex: 'stationName',
    headAlign: 'center',
    align: 'left',
    width: 220,
    resizable: true,
  },
  { title: '车辆编号', dataIndex: 'vehicleNo', width: 120, resizable: true },
  {
    title: '车牌号',
    dataIndex: 'plateNumber',
    width: 120,
    resizable: true,
    customRender: ({ text, record }) => {
      const url = '/po/integrated/vehicle/create';
      const params = { id: record.id, isReadonly: true };
      return useRender.renderIframeLink(url, undefined, params, text);
    },
  },
  { title: '车辆负责人', dataIndex: 'header', width: 100, resizable: true },
  { title: '驾驶员', dataIndex: 'responsiblePersonName', width: 200, resizable: true },
  {
    title: '下次年检日期',
    dataIndex: 'nextCheckDate',
    width: 150,
    resizable: true,
    customRender: ({ column, text, record }) => {
      const value = record[column.dataIndex + 'Color'];
      if (value) {
        return useRender.renderTag(text, statusColor[value]);
      }
      return text;
    },
  },
  { title: '最近一次保养日期', dataIndex: 'maintenanceTime', width: 150, resizable: true },
  { title: '最近一次车检日期', dataIndex: 'inspectDate', width: 150, resizable: true },
  {
    title: '保险类型',
    dataIndex: 'insureType',
    width: 100,
    resizable: true,
    customRender: ({ column, record }) => {
      return record[`${column.dataIndex}Text`];
    },
  },
  {
    title: '保险到期日期',
    dataIndex: 'endDate',
    width: 150,
    resizable: true,
    customRender: ({ column, text, record }) => {
      const value = record[column.dataIndex + 'Color'];
      if (value) {
        return useRender.renderTag(text, statusColor[value]);
      }
      return text;
    },
  },
];

