export const getFormSearchSchemas = (stationParams): FormSchema[] => {
  return [
    {
      field: 'stationOrganId',
      label: '所属公司',
      component: 'ApiSelect',
      componentProps: ({ formModel }) => {
        return {
          api: async () => {
            const data = await getOrgList({});
            const options: any = [];
            if (data && data.length > 0) {
              data.forEach((item) => {
                options.push({ value: item.id, label: item.label });
              });
            }
            return options;
          },
          onChange: (value) => {
            stationParams.organId = value;
            formModel.stationIds = [];
          },
        };
      },
      show: useUserStore().isMultiOrganization,
    },
    {
      field: 'stationIds',
      label: '电站名称',
      component: 'ApiSelect',
      componentProps: {
        api: async () => {
          const data = await CommonApi.getStationList({ ...stationParams });
          const options: any = [];
          if (data && data.length > 0) {
            data.forEach((item) => {
              options.push({ value: item.id, label: item.name });
            });
          }
          return options;
        },
        mode: 'multiple',
        maxTagCount: 'responsive',
        filterOption: (input: string, option: any) => {
          return option.label.toLowerCase().indexOf(input.toLowerCase()) >= 0;
        },
      },
    },
    {
      field: 'plateNumber',
      label: '车牌号',
      component: 'Input',
    },
    {
      field: 'responsiblePersonName',
      label: '驾驶员',
      component: 'Input',
    },
    {
      field: 'nextCheck',
      label: '下次年检日期',
      component: 'RangePicker',
      componentProps: {
        format: 'YYYY-MM-DD',
        valueFormat: 'YYYY-MM-DD',
        placeholder: ['开始日期', '结束日期'],
        style: 'width:100%',
      },
    },
    {
      field: 'insuranceExpire',
      label: '保险到期日期',
      component: 'RangePicker',
      componentProps: {
        format: 'YYYY-MM-DD',
        valueFormat: 'YYYY-MM-DD',
        placeholder: ['开始日期', '结束日期'],
        style: 'width:100%',
      },
    },
  ];
};
